export default {
	name: 'hx-preview-img',
	props: {
		current: {
			type: String,
			default: ''
		},
		list: {
			type: Array,
			default: function(){
				return new Array();
			}
		},
		//开始
		start: {
		    type: Number,
			default: 0
		},
		
	},
		
	data() {
		return {
			picList: [],
			indicatorDots: false,
			autoplay: false,
			duration: 500,
			circular: true,
			currentIndex: 0,
			isShowSwiper: false,
			showBox: false
		};
	},
	watch: {
		list(val) {
			
		},
		current(val){
			
		},
		start(val,oldVal) {
			var that = this;
			this.picListInit();
		}
	},
	created() {
		
	},
	methods: {
		clickPic(index) {
			this.currentIndex = index;
			this.isShowSwiper = true;
		},
		picListInit() {
			this.showBox = true;
			if(!this.current || !this.list){
				return;
			}
			for(var i in this.list){
				if(this.current == this.list[i]){
					this.currentIndex = i;
					break;
				}
			}
			
			this.picList=this.list;
			
		},
		changeSwiper(e) {
			this.currentIndex = e.target.current;
		},
		mpClear(e) {
			// TODO nvue 取消冒泡
			e.stopPropagation()
		},
		close(){
			this.showBox = false;
		}
	}
}
